import React, { Component } from 'react';

import msg from "../../_shared/msg";


export default class SearchBar extends Component {

    search(e) {
        e.preventDefault();
        var data = this.getFormData();
        if (this.props.load) {
            this.props.load(data);
        }
    }

    getFormData() {
        var data = {};
        data["IDKey"] =parseInt($('#IDKey').val());
        data["name"] = $("#name").val();
        data["deptName"] = $('#deptName').val();
        data["srcOrgName"] = $("#srcOrgName").val();
        data["fRelationStatus"] = $("#fRelationStatus").val();
        return data;
    }

    render() {
        return (
            <form className="form-inline">
                <div className="form-group">
                    <label>IDKey</label>&nbsp;
                    <input type="text" className="form-control" id="IDKey" placeholder="Id" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="name" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="deptName" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="srcOrgName" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="fRelationStatus" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>

                <button type="submit" className="btn btn-default" onClick={this.search.bind(this)}></button>
            </form>
        );
    }
}


