import React, { Component } from 'react';

import { msg } from "../../_shared/tool";



export default class Action extends Component {
    render() {
        return (
            <div className="btn-group">
                <button data-toggle="dropdown" className="btn btn-default dropdown-toggle" type="button" aria-expanded="true">
                    <span className="caret"></span>
                </button>
                <ul style={{ minWidth: 30 }} role="menu" className="dropdown-menu">
                    <li>
                        <a href="javascrpit:void(0)" onClick={this.props.EditPassword}></a>
                    </li>
                    <li>
                        <a href="javascrpit:void(0)" onClick={this.props.EditMobile}></a>
                    </li>
                    <li>
                        <a href="javascrpit:void(0)" onClick={this.props.EditState}></a>
                    </li>
                    <li>
                        <a href="javascrpit:void(0)" onClick={this.props.Delete}></a>
                    </li>
                </ul>
            </div>
        );
    }
}


