import React, { Component } from 'react';


export default class SearchBar extends Component {

    search(e) {
        e.preventDefault();
        var data = this.getFormData();
        if (this.props.Actions.User.search)
            this.props.Actions.User.search(data);
    }

    getFormData() {
        var data = {};
        data["username"] = $('#username').val();
        data["name"] = $("#name").val();
        data["idCardNo"] = $('#idCardNo').val();
        data["mobile"] = $("#mobile").val();
        data["email"] = $("#email").val();
        return data;
    }

    render() {
        return (
            <form className="form-inline">
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="text" className="form-control" id="username" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="name" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="idCardNo" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="mobile" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>
                <div className="form-group">
                    <label></label>&nbsp;
                    <input type="email" className="form-control" id="email" placeholder="" />&nbsp;&nbsp;&nbsp;
                </div>

                <button type="submit" className="btn btn-default" onClick={this.search.bind(this)}></button>
            </form>
        );
    }
}


